import Axios from "axios";
export default {

    getThemes: async (shop, accessToken) => {
        const error = new Error();
        try {
            const response = await Axios.get(process.env.NEXT_PUBLIC_API_URL + "/themes/get", { params: { shop: shop, accessToken: accessToken } });
            return response
        } catch (err) {
            error.status = err.response.data.status;
            error.message = err.response.data.message;
            throw error;
        }
    },
    installThemes: async (shop, accessToken, themeId) => {
        const error = new Error();
        try {
            const response = await Axios.post(
                process.env.NEXT_PUBLIC_API_URL + "/themes/setup",
                { shop: shop, accessToken: accessToken, theme_id: `${themeId}` }
            );
            return response;

        }
        catch (err) {
            error.status = err.response.data.status;
            error.message = err.response.data.message;
            throw error;
        }


    },
    removeTheme: async (shop, accessToken, themeId) => {
        const error = new Error();
        try {
            const response = await Axios.post(
                process.env.NEXT_PUBLIC_API_URL + "/themes/remove",
                { shop: shop, accessToken: accessToken, theme_id: `${themeId}` }
            );
            return response;

        }
        catch (err) {
            error.status = err.response.data.status;
            error.message = err.response.data.message;
            throw error;
        }


    },
};