import React, { useState, useEffect, useContext } from 'react'
import style from "./styles.module.css";
import ThemeServices from './services/themeServices';
import { Col, Divider, Row, Card, Select, Button, message } from "antd";



const lColProps = {
    sm: 24,
    md: 7,
    lg: 7,
    xs: 24,
};
const rColProps = {
    sm: 24,
    md: 14,
    lg: 17,
    xs: 24,
};

const AddRow = (data) => {
    return (
        <Row gutter={40} >
            <Col {...lColProps} >
                <h1 className={style.createDiscountHeading}>
                    {data.title}
                </h1>
                <p className={style.discountDescreption}>
                    {data.description}
                </p>
            </Col>
            <Col {...rColProps}

            >
                <Card style={styles.card}>
                    {data.children}

                </Card>
            </Col>
        </Row>)
}
const Installation = () => {

    const [themeId, setThemeId] = useState('');
    const [updatingTheme, setUpdateTheme] = useState(false);
    const [removeTheme, setRemoveTheme] = useState(false);
    const [themesList, setThemeList] = useState();

    const getInitalThemes = async () => {
        try {
            const response = await ThemeServices.getThemes(1, 1);
            if (response) {
                setThemeList(response.data.data);
            }
        }
        catch (err) {
            message.error(err.message);
        }
    }

    // useEffect(() => {
    //     getInitalThemes();
    // }, []);


    const onThemeChange = (value) => {
        setThemeId(value);

    }

    const setUpWidget = async () => {
        try {
            if (themeId) {

                setUpdateTheme(true)
                await ThemeServices.installThemes(1, 1, themeId);
                setUpdateTheme(false)
                message.success("Widget has been added")
            } else {
                message.warn("Please select theme")
            }
        } catch (err) {
            setUpdateTheme(false)
            message.error(err.message)
        }
    }
    const removeWidget = async () => {
        try {
            if (themeId) {
                setRemoveTheme(true)
                await ThemeServices.removeTheme(1, 1, themeId);
                setRemoveTheme(false)
                message.success("Widget has been removed")
            } else {
                message.warn("Please select theme")
            }
        } catch (err) {
            setRemoveTheme(false)
            message.error(err.message)
        }
    }





    return (

        <section>
            <Row justify="center" align="middle">
                <Col span={1}></Col>
                <Col span={21}>
                    <Row >
                        <Col span={24}>
                            <h1 style={styles.heading}>Wishlist Application</h1>
                            <p style={styles.para}>
                                Installing snippets may require you to modify Liquid files and might require the help of a developer. If you have any questions, please reach out to our team.</p>
                        </Col>

                    </Row>
                    <Divider />
                    <AddRow title={"Select Theme"}
                        description="Choose a theme to install Subscription in"
                    >
                        <Select style={{ minWidth: 320 }} defaultValue="Choose Theme" onChange={(value) => onThemeChange(value)} loading={!themesList}>
                            {themesList && themesList.length > 0 ? themesList.map(entry => {
                                return <Select.Option value={entry.id}>{entry.name}</Select.Option>
                            }) : ""}
                        </Select>

                    </AddRow>
                    <Divider />
                    <AddRow
                        title="Automatic Installation in Theme Supply"
                        description="Automatically setup the subscription app in the selected theme.">

                        <Button onClick={setUpWidget} loading={updatingTheme}>Setup Widget</Button>
                    </AddRow>
                    <Divider />
                    <AddRow
                        title="Remove Subscription Widget"
                        description={"Uninstall and remove the Liquid code in theme: " + themeId}>

                        <Button onClick={removeWidget} loading={removeTheme}>Remove Widget</Button>
                    </AddRow>

                    <Divider />


                </Col>
            </Row>
        </section>

    );
}
const styles = {
    card: {
        width: '800px',
        boxShadow: " 2px 2px 2px 2px #ccc",
        borderRadius: 3,
    },
    heading: {
        fontWeight: "bold",
        fontSize: 20,
        marginBottom: 10,
    },
    para: {
        color: "grey",
        fontSize: 14,
        marginBottom: 10,
        fontWeight: "bold",
    },
    spacing: {
        marginTop: 20,
    },
};
export default Installation;
